<template>
    <div>
        <ul class="plugin-list">
            <li>
                <h3>Name：</h3>
                <div class="text">DatePicker</div>
            </li>
            <li>
                <h3>Introduction：</h3>
                <div class="text">移动端的日期选择器插件</div>
            </li>
            <li>
                <h3>Demo：</h3>
                <p class="btn" @click.stop="onClickDate()">demo</p>
                <div class="text">
                    您选择的日期为：<input type="text" readonly="readonly" v-model="nowVal">
                </div>
            </li>
            <li>
                <h3>Function：</h3>

<pre class="JS">
new DatePicker({
    //0年, 1年月, 2月日, 3年月日(默认为3)
    "type": "3",

    //标题（可选）
    "title": '请选择日期',

    //最大年份（可选）
    "maxYear": "2100",

    //最小年份（可选）
    "minYear": "1900",

    //日期分割符(可选)(默认为'/')
    "separator": "-",

    //默认值（可选）
    "defaultValue": '',

    "callBack": function (val) {
        //回调函数（val为选中的日期）
        self.nowVal = val;
    }
});
</pre>
            </li>
            <li>
                <h3>Usage：</h3>
                <div class="text">
                    引入文件：</br>
                    'datePicker/picker.css'</br>
                    'datePicker/datePicker.min.js'
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    import DatePicker from '../../lib/datePicker/datePicker'

	export default {
		name: 'datePicker',
        data(){
            return {
                nowVal: ''
            }
        },
        methods: {
            // 日期选择器
            onClickDate(){
                var self = this;
                new DatePicker({
                    "type": "3",//0年, 1年月, 2月日, 3年月日(默认为3)
                    "title": '请选择日期',//标题(可选)
                    "maxYear": "",//最大年份（可选）
                    "minYear": "",//最小年份（可选）
                    "separator": "-",//分割符(可选)(默认为'/')
                    "defaultValue": self.nowVal,//默认值（可选）
                    "callBack": function (val) {
                        //回调函数（val为选中的日期）
                        self.nowVal = val;
                    }
                });
            }
        }
	}
</script>

<style scoped>
    /*app.vue 模块中已引入*/
    /*@import "../../lib/datePicker/picker.css";*/
</style>


